
.nodes-table {
  overflow: visible;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
table.nodes {
  min-width: 100%;
}
table.nodes thead tr {
  min-width: 100%;
}
table.nodes tbody {
  overflow-y: auto;
  overflow-x: visible;
}
table.nodes tr.full {
  min-width: 100%;
}
table.nodes td,
table.nodes th {
  will-change: trasform;
  -webkit-animation-name: row-anim;
          animation-name: row-anim;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}
table.nodes .node-history,
table.nodes .node-chart {
  height: 50px;
  padding: 0;
  max-width: 10em;
  min-width: 5em;
  max-height: 2em;
  margin: 0;
}
table.nodes .node-history svg path,
table.nodes .node-chart svg path {
  stroke-width: 1px;
}
@-webkit-keyframes row-anim {
0% {
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
}
50% {
    -webkit-transform: rotateX(45deg) rotateZ(0);
            transform: rotateX(45deg) rotateZ(0);
}
100% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
}
}
@keyframes row-anim {
0% {
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
}
50% {
    -webkit-transform: rotateX(45deg) rotateZ(0);
            transform: rotateX(45deg) rotateZ(0);
}
100% {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
}
}
.search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
