  margin-right: 0.5em;
}
.hidden-fields {
  z-index: 500;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-ordinal-group: 3;
      -ms-flex-order: 2;
          order: 2;
}
.hidden-fields button {
  margin-left: 0.5em;
}
tr.field-actions {
  padding: 0;
