
.d3-bar-chart{max-height:100%;max-width:100%
}
.d3-bar-chart svg{overflow:visible
}
.bar{fill:#0ff;stroke:none
}
.dummy-bar{fill:none;stroke:none;pointer-events:all
}
.dummy-bar.has-bars:hover{fill:rgba(0,0,0,.1)
}
.bar-text{fill:gray
}
.rulers{stroke-width:1px;stroke:#000
}
.lines{stroke:gray;stroke-opacity:.3
}
.axis,.lines{stroke-width:1px
}
.axis{stroke:gray
}
.axis-label{fill:gray;stroke:none;font-size:8px
}
.line{stroke:rgba(0,0,0,.5);stroke-width:2px
}
.curve,.curve-back{stroke:#000;stroke-width:3px;fill:none
}
.curve-point{fill:gray;stroke:#000
}
.chart-tip-back{fill:#000;stroke-width:1px;stroke:gray;opacity:.5
}


