
.app-menu .panel.tools {
  padding-bottom: 1em;
}
.app-menu .panel.tools button.badge {
  margin-top: 0;
}
.slide-r {
  border: #d7343e solid 2px;
}
.slide-r-enter-active,
.slide-r-leave-active {
  will-change: transform;
  transition: all 0.5s ease;
  position: fixed;
}
.slide-r-enter,
.slide-r-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}


