
.big-data {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #1aad8d;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 100%;
  height: auto;
  border: solid rgba(26,173,141,0.2) 1px;
  margin-top: 0.5em;
  z-index: 100;
  pointer-events: all;
  background: #112329;
  overflow: visible;
  min-height: 4.75rem;
}
.big-data .bd-icon {
  width: 3em;
  height: 3em;
  color: #1aad8d;
  opacity: 0.6;
  box-sizing: border-box;
  margin-right: 0.125rem;
  margin-left: 1em;
}
.big-data .bd-icon .svg-icon {
  width: 3em;
  height: 3em;
}
.big-data .bd-main {
  width: auto;
  display: inline-block;
  margin-left: 1em;
}
.big-data .bd-main .bd-title {
  opacity: 0.8;
  font-size: 0.9em;
  margin-left: 0.5em;
  margin-top: 0.25em;
  letter-spacing: 0.0625em;
}
.big-data .bd-main .bd-title small::before {
  content: ' ';
}
.big-data .bd-main .bd-data {
  font-size: 2.5rem;
  line-height: 3rem;
  min-height: 2.5rem;
}
.big-data .bd-main .bd-data.big-number {
  font-size: 1.25rem;
}
.big-data.mini {
  min-height: auto;
}
.big-data.mini .bd-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.big-data.mini .bd-main .bd-title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 0;
  margin-right: 0.5em;
  font-size: 80%;
}
.big-data.mini .bd-data {
  font-size: 1.25rem;
}
.big-data.mini .bd-icon {
  margin-right: 0;
  width: 1.5em;
  height: 1.5em;
}
.big-data.mini .bd-icon .svg-icon {
  width: 1.5em;
  height: 1.5em;
}
.totals-dialog {
  background: none;
  box-shadow: none;
}
.totals-dialog .big-data {
  margin-top: 0;
  background: #142830;
}
.totals-dialog .buttons,
.totals-dialog .dialog-header {
  position: absolute;
  top: 1em;
  right: 0;
  height: 1em;
  margin: 0;
}
.totals-dialog button.close {
  position: absolute;
  z-index: 1000;
  pointer-events: all;
  width: 1em;
  height: 1em;
  right: 0;
  top: -2em;
}
.totals-dialog .dialog-body {
  padding: 0;
}


