
.tooltip,
.trim {
  position: relative;
  display: inline-block;
  overflow: visible;
  z-index: 1000;
}
.nowrap {
  white-space: nowrap;
}
.tooltip {
  position: relative;
}
.tooltip .tip {
  position: absolute;
  -webkit-filter: drop-shadow(1px 1px 2px rgba(0,0,0,0.7));
          filter: drop-shadow(1px 1px 2px rgba(0,0,0,0.7));
  width: 100%;
  color: #127862;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.tooltip .tip .value {
  border-radius: 3px;
  padding: 0.125em 0.25em;
  background-color: #fff;
  word-break: break-all;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.tooltip .tip .tip-txt {
  padding: 0.25em;
  overflow: visible;
  display: inline;
  margin: 0;
  font-size: 0.9rem;
}
.tooltip .tip:after,
.tooltip .tip:before {
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  z-index: 100;
}
.tooltip .tip:after {
  border-width: 5px;
}
.tooltip .tip:before {
  border-width: 6px;
}
.tooltip .tip.top:after,
.tooltip .tip.top:before {
  top: 100%;
  left: 50%;
}
.tooltip .tip.top:after {
  border-top-color: #fff;
  margin-left: -5px;
}
.tooltip .tip.top:before {
  border-top-color: #1aad8d;
  margin-left: -6px;
}
.tooltip .tip.bottom:after,
.tooltip .tip.bottom:before {
  bottom: 100%;
  left: 50%;
}
.tooltip .tip.bottom:after {
  border-bottom-color: #fff;
  margin-left: -5px;
}
.tooltip .tip.bottom:before {
  border-bottom-color: #1aad8d;
  margin-left: -6px;
}
.tooltip .tip.left:after,
.tooltip .tip.left:before {
  left: 100%;
  bottom: 50%;
}
.tooltip .tip.left:after {
  border-left-color: #fff;
  margin-bottom: -5px;
}
.tooltip .tip.left:before {
  border-left-color: #1aad8d;
  margin-bottom: -6px;
}
.tooltip .tip.right:after,
.tooltip .tip.right:before {
  right: 100%;
  bottom: 50%;
}
.tooltip .tip.right:after {
  border-right-color: #fff;
  margin-bottom: -5px;
}
.tooltip .tip.right:before {
  border-right-color: #1aad8d;
  margin-bottom: -6px;
}
.tooltip .points {
  display: inline-block;
  box-shadow: none;
  margin: 0 0.25em;
  line-height: 0.5em;
  margin-top: 0.25em;
}
.tooltip .points button {
  height: 1em;
  line-height: 1em;
}
.tooltip .points.left {
  float: left;
}
.tooltip .points.right {
  float: right;
}
.tooltip button.copy {
  display: block;
  position: absolute;
  left: 0;
  top: 0.25em;
  line-height: 1em;
  font-size: 1em;
  margin: 0 0.5em;
}
.tooltip button.copy .icon {
  font-size: 1em;
}
.tooltip .copy-txt {
  display: inline;
}
.tooltip .copy-txt textarea {
  border: none;
  margin: 0;
  padding: 0;
  background-color: inherit;
  opacity: 0;
  width: 1px;
  heigth: 1px;
}
.tooltip button.close {
  line-height: 1em;
  height: 1em;
}
.tooltip button.close:after {
  top: 0.25em !important;
  right: 0.25em !important;
  border-radius: 50%;
  line-height: 1em;
  height: 1em;
  width: 1em;
  padding: 0.25em;
}
.head {
  display: block;
}
.msg {
  color: #1aad8d;
  position: absolute;
  top: -1.5em;
  left: 0;
  width: 100%;
  font-size: 0.8em;
  opacity: 0;
}
.anim {
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-name: copya;
          animation-name: copya;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  opacity: 0;
}
@-webkit-keyframes copya {
0% {
    opacity: 0;
}
75% {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em);
}
80% {
    opacity: 1;
}
100% {
    opacity: 0;
    -webkit-transform: translateY(-5em);
            transform: translateY(-5em);
}
}
@keyframes copya {
0% {
    opacity: 0;
}
75% {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em);
}
80% {
    opacity: 1;
}
100% {
    opacity: 0;
    -webkit-transform: translateY(-5em);
            transform: translateY(-5em);
}
}
.copying {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: copyb;
          animation-name: copyb;
}
@-webkit-keyframes copyb {
0% {
    color: #1aad8d;
}
100% {
    color: inherit;
}
}
@keyframes copyb {
0% {
    color: #1aad8d;
}
100% {
    color: inherit;
}
}


