
.node-info {
  box-sizing: border-box;
  position: absolute;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.node-info .entity-value {
  position: absolute;
}
.node-info svg {
  overflow: visible;
  position: absolute;
}
.node-info svg.cube path {
  stroke: #000;
  stroke-width: 1;
  stroke-opacity: 0.25;
}
.node-info .cubes .cube {
  stroke: #102026;
  stroke-width: 0.5;
  stroke-opacity: 0.85;
}
.node-info .node-info-data {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.node-info .over-node {
  opacity: 0.6;
}
.node-info .block-number {
  color: #1aad8d;
  font-size: 0.8em;
  opacity: 0;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  will-change: transform, opacity;
}
.node-info .block-number path {
  fill: none;
  stroke: #1aad8d !important;
  stroke-width: 1 !important;
  stroke-opacity: 1 !important;
  stroke-dasharray: 5;
}
.node-info .block-number-enter-active {
  transition: all 1s ease;
  opacity: 0;
  -webkit-transform: translateY(-5rem) scale(1.5, 1.5);
          transform: translateY(-5rem) scale(1.5, 1.5);
}
.node-info .block-number-enter {
  opacity: 1;
  -webkit-transform: translateY(1rem) scale(0.1, 0.1);
          transform: translateY(1rem) scale(0.1, 0.1);
}
.node-info .block-cube {
  will-change: opacity transform;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  fill: #1aad8d;
  opacity: 1;
}
.node-info .block-cube .cube {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  will-change: opacity transform;
  -webkit-animation: bcube-anim 0.5s ease;
          animation: bcube-anim 0.5s ease;
  opacity: 0;
}
.node-info .block-cube-enter {
  opacity: 0;
}
.node-info .block-cube-enter-active {
  transition: all 2s ease-in;
  opacity: 1;
}
.node-info .trans-value {
  fill: #127862;
}
.node-info .time {
  -webkit-animation: etime 3s infinite;
          animation: etime 3s infinite;
}
@-webkit-keyframes bcube-anim {
0% {
    opacity: 0;
    -webkit-transform: translate(100%, 80%) scale(0.1, 0.1);
            transform: translate(100%, 80%) scale(0.1, 0.1);
}
opacity 1 {
    -webkit-transform: translate(50%, 100%) scale(1, 1);
            transform: translate(50%, 100%) scale(1, 1);
}
60% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}
90% {
    opacity: 1;
}
100% {
    -webkit-transform: translate(-250%, -110%);
            transform: translate(-250%, -110%);
    opacity: 0;
}
}
@keyframes bcube-anim {
0% {
    opacity: 0;
    -webkit-transform: translate(100%, 80%) scale(0.1, 0.1);
            transform: translate(100%, 80%) scale(0.1, 0.1);
}
opacity 1 {
    -webkit-transform: translate(50%, 100%) scale(1, 1);
            transform: translate(50%, 100%) scale(1, 1);
}
60% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
}
90% {
    opacity: 1;
}
100% {
    -webkit-transform: translate(-250%, -110%);
            transform: translate(-250%, -110%);
    opacity: 0;
}
}
@-webkit-keyframes etime {
0% {
    opacity: 0.1;
}
50% {
    opacity: 0.8;
}
100% {
    opacity: 0.1;
}
}
@keyframes etime {
0% {
    opacity: 0.1;
}
50% {
    opacity: 0.8;
}
100% {
    opacity: 0.1;
}
}


